<template>
  <section :class="[panelClass, cname]">
    <h4>{{ title }}</h4>
    <!--  Panel -->
    <slot/>
  </section>
</template>

<script>
export default {
  props:{
    //  cname
    cname:{
      type: String,
      default: "",
    },
    // 
    title:{
      type: String,
      default:"",
    }
  },
  data(){
    return{
      panelClass: "panel",
    }
  }
}
</script>

<style lang="scss" module>
@import "../../css/element.scss";
.panel{
  @include panel;
  // background: #fff;
}
</style>
